.TodoList {padding:0 20px;}

.TodoList h4{
  font-size: 1.2rem;
  font-family: "Roboto", 'PretendardM';
  font-weight: 700;
  font-style: normal;
  color:#5f8cff;
  margin-bottom: 10px;
}

.TodoList input {
  box-sizing: border-box;
  font-family: "Roboto", 'PretendardM';
  /* font-weight: 400; */
  font-size: 0.8rem;
  font-style: normal;
  color: #5f8cff;
}


.TodoList input::placeholder {color:#b1b1bb; font-family: 'PretendardR'}

/* 검색 폼에 스타일 적용 */
.TodoList .searchbar {
  margin-bottom: 20px;
  width: 100%;
  border: none;
  border-bottom: 1px solid #5f8cff;
  box-sizing: border-box;
  padding-top: 15px;
  padding-bottom: 15px;
}

/* 검색 폼을 클릭했을 때의 스타일 적용 */
.TodoList .searchbar:focus {
  outline: none;
  border-bottom: 1px solid #1f93ff;
}

.TodoList .list_wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
