.TodoEditor .editor_wrapper {
  width: 100%;
  display: flex;
  gap: 10px;
}

.TodoEditor {padding:0 20px;}

.TodoEditor h4{
  font-size: 1.2rem;
  font-family: "Roboto", 'PretendardM';
  font-weight: 700;
  font-style: normal;
  color:#5f8cff;
  margin-bottom: 10px;
}

.TodoEditor input {
  flex: 1;
  box-sizing: border-box;
  /* border: 1px solid rgb(220, 220, 220); */
  border: 1px solid #5f8cff;
  border-radius: 5px;
  padding: 15px;
  font-family: "Roboto",'PretendardM'; 
  font-weight: 400;
  font-style: normal;
  color:#5f8cff;
  font-size: 0.8rem;
}

.TodoEditor input::placeholder {color:#b1b1bb; font-family: 'PretendardR'}


.TodoEditor input:focus {
  outline: none;
  border: 1px solid #5f8cff;
}

.TodoEditor button {
  cursor: pointer;
  width: 50px;
  border: none;
  background-color: #5f8cff;
  color: #00ffae;
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  border-radius: 5px;
}
